import React from 'react';
import NotFoundLayout from '../layout';
import { Button } from '@styles/Button';

function UnknownError() {
  return (
    <NotFoundLayout>
      <div>
        <h1>   ..</h1>
        <div>
          <p>   ,  .</p>
        </div>
      </div>
      <div className="btn_group">
        <Button
          onClick={() => {
            window.location.reload();
          }}
        >
          
        </Button>
        <Button
          onClick={() => {
            window.location.href = '/';
          }}
        >
           
        </Button>
      </div>
    </NotFoundLayout>
  );
}

export { UnknownError };
