import React from 'react';

import { Button } from '@styles/Button';
import NotFoundLayout from '../layout';

function NotFoundRoom() {
  return (
    <NotFoundLayout>
      <div>
        <h1>   !</h1>
        <div>
          <p>,   ...</p>
        </div>
      </div>
      <div className="btn_group">
        <Button
          onClick={() => {
            window.location.href = '/select-room';
          }}
        >
            
        </Button>
      </div>
    </NotFoundLayout>
  );
}

export { NotFoundRoom };
